
import { defineComponent } from "vue";

export default defineComponent({
  name: "custom-input",
  props: {
    type: String,
    value: String,
  },
  emits: ["update:modelValue"],
  methods: {
    onChange(event: Event) {
      const target = event.target as HTMLInputElement;
      this.$emit("update:modelValue", target.value);
    },
  },
});
